<template>
	<div id="creditsign-index">
		<public-header :title="`积分${config.text.sign}`"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="banner" :style="{backgroundColor: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}">
				<div class="avatar">
					<img :src="member.avatar" alt="">
				</div>
				<router-link :to="util.getUrl({path: '/plugin/pages/creditsign/record'})" class="detail">详细记录</router-link>
				<div @click="agreementShow = true" class="agreement">活动规则</div>
				<div class="gutter">
					<div class="col-33">
						<div class="titles">连续{{config.text.sign}}</div>
						<div class="days">{{credit_days_amount.continuous}}天</div>
					</div>
					<div class="col-33">
						<div class="titles">{{member.nickname}}</div>
					</div>
					<div class="col-33">
						<div class="titles">累计{{config.text.sign}}</div>
						<div class="days">{{credit_days_amount.sum}}天</div>
					</div>
				</div>
			</div>
			<div class="calendar">
				<div class="titles" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}">
					<van-icon name='calendar' class="icon"></van-icon>
					{{year}}年{{month}}月
				</div>
				<div class="calendar-inner">
					<div class="week" v-for="week in calendar">
						<template v-for="item in week">
							<template v-if="item.today">
								<div class="day" :class="{today: item.today}" @click="onDayClick(item.day, item.grant)" :style="{borderColor: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}">
									<p class="num">{{item.day ? item.day : ''}}</p>
									<p class="grant"><van-icon v-if="item.grant" name='recharge' class="icon" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}"></van-icon>{{item.grant}}</p>
									<p class="special" v-if="item.title">{{item.title}}</p>
									<template v-if="item.signed">
										<van-icon name='check' :class="{sigend: item.signed}" :style="{color: config.style.maincolor ? config.style.maincolor: '#ff2d4b'}"></van-icon>
									</template>
								</div>
							</template>
							<template v-else>
								<div class="day" @click="onDayClick(item.day, item.grant)">
									<p class="num">{{item.day ? item.day : ''}}</p>
									<p class="grant"><van-icon v-if="item.grant" name='recharge' class="icon" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}"></van-icon>{{item.grant}}</p>
									<p class="special" v-if="item.title">{{item.title}}</p>
									<template v-if="item.signed">
										<van-icon name='check' :class="{sigend: item.signed}" :style="{color: config.style.maincolor ? config.style.maincolor: '#ff2d4b'}"></van-icon>
									</template>
								</div>
							</template>
						</template>

					</div>
				</div>
			</div>
			<div class="reward-container" v-if="config.reward.continue.length > 0">
				<div class="titles">
					<span class="text"><van-icon name='gifts' class="icon"></van-icon> 连续{{config.text.sign}}有礼</span>
					<span class="subtitle pull-right">连续{{config.text.sign}}即可领取</span>
				</div>
				<div class="reward-inner">
					<div class="award">
						<div class="award-list">
							<div class="item" :class="{active: continuous_get[continuous.day]}" v-for="(continuous, index) in config.reward.continue">
								<div class="grant">+{{continuous.credit}}<br>{{grantType}}</div>
								<template v-if="continuous_get[continuous.day]">
									<div class="circle" :style="{backgroundColor: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}" :class="{active: continuous.status == 2}"><i class="icon icon-check"></i> </div>
									<div class="days" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}">{{continuous.day}}天</div>
								</template>
								<template v-else>
									<div class="circle" :class="{active: continuous.status == 2}"><i class="icon icon-check"></i> </div>
									<div class="days">{{continuous.day}}天</div>
								</template>

								<div class="text" v-if="!continuous.status" @click="onGetReward()">不可领</div>
								<div class="text" v-if="continuous.status == 1" @click="onGetReward(1, continuous.day, continuous.status, index)">可领取</div>
								<div class="text" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b', borderColor: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}" v-if="continuous.status == 2" @click="onGetReward(1, continuous.day, continuous.status, index)">已领取</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="reward-container" v-if="config.reward.total.length > 0">
				<div class="titles">
					<span class="text"><van-icon name='gifts' class="icon"></van-icon> 累计{{config.text.sign}}有礼</span>
					<span class="subtitle pull-right">累计{{config.text.sign}}满足即可领取</span>
				</div>
				<div class="reward-inner">
					<div class="award">
						<div class="award-list">
							<div class="item" :class="{active: all_get[all.day]}" v-for="(all, index) in config.reward.total">
								<div class="grant">+{{all.credit}}<br>{{grantType}}</div>
								<template v-if="all_get[all.day]">
									<div class="circle" :class="{active: all.status == 2}" :style="{backgroundColor: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}"><i class="icon icon-check"></i> </div>
									<div class="days" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}">{{all.day}}天</div>
								</template>
								<template v-else>
									<div class="circle" :class="{active: all.status == 2}"><i class="icon icon-check"></i> </div>
									<div class="days">{{all.day}}天</div>
								</template>
								<div class="text" v-if="!all.status" @click="onGetReward()">不可领</div>
								<div class="text" v-if="all.status == 1" @click="onGetReward(2, all.day, all.status, index)">可领取</div>
								<div class="text" :style="{color: config.style.maincolor ? config.style.maincolor : '#ff2d4b', borderColor: config.style.maincolor ? config.style.maincolor : '#ff2d4b'}" v-if="all.status == 2" @click="onGetReward(2, all.day, all.status, index)">已领取</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<van-popup v-model="agreementShow" position="bottom" class="agreement-popup">
			<van-nav-bar class="border-0px" title="规则详解" @click-left="agreementShow = false" :style="{background: config.style.maincolor ? config.style.maincolor : '#ff2d4b', color: '#fff' }">
				<van-icon name="left" :style="{color: '#fff'}" slot="left" class="font-20"></van-icon>
			</van-nav-bar>
			<div class="popup-content margin-10" v-html="config.agreement"></div>
		</van-popup>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	export default {
		components: {
			PublicHeader,
			PublicFooter
		},
		data() {
			return {
				showPreLoading:true,
				agreementShow: false,
				year:'',
				month:'',
				calendar: [],
				continuous_get: [],
				all_get: [],
				credit_days_amount: {},
				member: {},
				config: {
					text: {
						sign: '签到'
					},
					style: {
						maincolor: '#ff2d4b'
					},
					reward: {
						total: [],
						continue: []
					}
				},
				grantType:'',
				menufooter: {}
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'creditsign/index',
					data: {
						menufooter: 1
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					result = result.message;
					this.year = result.year;
					this.month = result.month;
					this.calendar = result.calendar;
					this.continuous_get = result.continuous_get;
					this.all_get = result.all_get;
					this.credit_days_amount = result.credit_days_amount;
					this.member = result.member;
					this.config = this.util.extend(this.config, result.config);
					this.grantType = result.grantType;
					this.menufooter = window.menufooter;
					this.$nextTick(() => {
						var navBartitle = document.getElementsByClassName('van-nav-bar');
						if(navBartitle && navBartitle.length > 0) {
							navBartitle[0].style.background = this.config.style.maincolor;
						}
					})
				})
			},

			onGetReward(type, days, status, index) {
				if(!type || !days) {
					this.$toast('您还没有达到领取该奖励的条件');
					return false;
				}
				this.util.request({
					url: 'creditsign/index/get',
					data: {
						days: days,
						type: type
					},
					method: 'POST'
				}).then((res) => {
					var result = res.data.message;
					this.$toast(result.message);
					if(result.errno) {
						return false;
					}
					if(status == 1){
						if(type == 1) {
							this.config.reward.continue[index].status = 2;
							this.continuous_get[days] = {days: days};
						} else if(type == 2) {
							this.config.reward.total[index].status = 2;
							this.all_get[days] = {days: days};
						}
					}
				})
			},

			onDayClick(day, grant) {
				var date = new Date();
				var today = date.getDate();
				var difference = day - today;
				this.util.request({
					url: 'creditsign/index/next',
					data: {
						difference: difference,
						grant: grant
					}
				}).then((res) => {
					this.$dialog.alert({
						message: res.data.message.message,
						showConfirmButton: false,
						closeOnClickOverlay: true
					})
				})
			},
		},
		mounted () {
			this.onLoad();
		},
	}
</script>

<style>
	#creditsign-index .banner{
		padding: 4px 0;
		text-align: center;
		color: #fff;
		background-color: #ff2d4b;
		background-size: cover;
		position: relative;
	}
	#creditsign-index .banner .avatar img{
		width: 55px;
		height: 55px;
		border-radius: 100%;
		border: 2px solid #fff;
		max-width: 100%;
	}
	#creditsign-index .banner .detail{
		position: absolute;
		height: 24px;
		line-height: 24px;
		width: 60px;
		color: #fff;
		font-size: 12px;
		text-align: center;
		background: rgba(255, 255, 255, 0.2);
		border-radius: 14px;
		right: 8px;
		top: 6px;
	}
	#creditsign-index .banner .agreement{
		position: absolute;
		height: 24px;
		line-height: 24px;
		width: 60px;
		color: #fff;
		font-size: 12px;
		text-align: center;
		background: rgba(255, 255, 255, 0.2);
		border-radius: 14px;
		left: 8px;
		top: 6px;
	}
	#creditsign-index .banner .gutter{
		overflow: hidden;
	}
	#creditsign-index .banner .gutter .col-33{
		width: 33.333333333333336%;
		margin: 0;
		float: left;
	}
	#creditsign-index .banner .gutter .col-33 .titles{
		font-size: 12px;
		color: #fff;
		line-height: 18px;
	}
	#creditsign-index .banner .gutter .col-33 .days{
		font-size: 17px;
		line-height: 25px;
	}
	#creditsign-index .calendar{
		margin-top: 10px;
		padding: 0 10px;
		overflow: hidden;
	}
	#creditsign-index .calendar .titles{
		height: 40px;
		padding: 0 10px;
		line-height: 40px;
		font-size: 16px;
		background: #fff;
		color: #ff2d4b;
		border-bottom: 1px solid #f2f2f2;
	}
	#creditsign-index .calendar .titles .icon{
		font-size: 16px;
		margin-right: 4px;
	}
	#creditsign-index .calendar .calendar-inner{
		background: #fff;
		padding: 10px;
	}
	#creditsign-index .calendar .calendar-inner .week:first-child{
		width: 100%;
		border: 0;
		overflow: hidden;

	}
	#creditsign-index .calendar .calendar-inner .week{
		width: 100%;
		border-top: 1px solid #f4f4f4;
		overflow: hidden;

	}
	#creditsign-index .calendar .calendar-inner .week .day:nth-child(7n+1){
		border: 0;
	}
	#creditsign-index .calendar .calendar-inner .week .day{
		height: 56px;
		width: 14%;
		border-left: 1px solid #f4f4f4;
		color: #777;
		font-size: 16px;
		position: relative;
		overflow: hidden;
		float: left;
	}
	#creditsign-index .calendar .calendar-inner .week .day.today{
		border: 1px solid #ff2d4b;
	}
	#creditsign-index .calendar .calendar-inner .week .day .num{
		height: 24px;
		line-height: 24px;
		padding-left: 6px;
		margin: 0;
	}
	#creditsign-index .calendar .calendar-inner .week .day .sigend{
		position: absolute;
		top: 4px;
		right: 8px;
		height: 16px;
		width: 16px;
		line-height: 16px;
		color: #ff2d4b;
		font-size: 22px;
	}
	#creditsign-index .calendar .calendar-inner .week .day .grant{
		margin: 0;
		height: 16px;
		font-size: 14px;
		line-height: 16px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		text-align: center;
		align-items: center;
	}
	#creditsign-index .calendar .calendar-inner .week .day .grant .icon{
		font-size: 13px;
		color: #ff2c4b;
	}
	#creditsign-index .calendar .calendar-inner .week .day .special{
		color: #fff;
		margin: 0;
		height: 16px;
		font-size: 12px;
		line-height: 16px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		text-align: center;
		position: absolute;
		bottom: 0;
		width: 100%;
		background: #ccc;
	}
	#creditsign-index .reward-container{
		margin-top: 4px;
		overflow: hidden;
		position: relative;
	}
	#creditsign-index .reward-container .titles{
		height: 32px;
		line-height: 32px;
		color: #888;
		font-size: 14px;
		padding: 0 6px;
	}
	#creditsign-index .reward-container .titles .text .icon{
		font-size: 16px;
	}
	#creditsign-index .reward-container .titles .subtitle{
		font-size: 12px;
		float: right;
	}
	#creditsign-index .reward-container .reward-inner{
		height: auto;
		width: 100%;
		overflow: hidden;
		position: relative;
	}
	#creditsign-index .reward-container .reward-inner:before{
		position: absolute;
		top: 42px;
		left: 0;
		right: 0;
		content: "";
		height: 5px;
		background: #fff;
	}
	#creditsign-index .reward-container .reward-inner .award{
		height: 114px;
		width: 100%;
		overflow-x: auto;
		overflow-y: hidden;
	}
	#creditsign-index .reward-container .reward-inner .award::-webkit-scrollbar {
		display: none;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list{
		height: 104px;
		width: auto;
		display: inline-flex;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item{
		height: 100px;
		width: 72px;
		overflow: hidden;
		position: relative;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item .grant{
		color: #666;
		font-size: 11px;
		height: 28px;
		width: 52px;
		padding-top: 4px;
		line-height: 12px;
		text-align: center;
		margin: auto;
		background: #fff;
		border-radius: 2px;
		position: relative;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item.active .circle{
		background: #ff2d4b;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item .circle{
		height: 20px;
		width: 20px;
		background: #fff;
		margin: 6px auto 0;
		border-radius: 28px;
		position: relative;
		color: #fff;
		font-size: 20px;
		font-weight: bold;
		line-height: 15px;
		text-align: center;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item.active .days{
		color: #ff2d4b;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item .days{
		color: #888;
		font-size: 12px;
		text-align: center;
		line-height: 18px;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item.active .text{
		color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#creditsign-index .reward-container .reward-inner .award .award-list .item .text{
		font-size: 12px;
		text-align: center;
		width: 48px;
		border: 1px solid #ccc;
		margin: auto;
		border-radius: 2px;
		line-height: 20px;
		color: #999;
	}
	#creditsign-index .agreement-popup{
		height: 100%;
		width: 100%;
	}
	#creditsign-index .van-nav-bar__title{
		color: #fff;
	}
</style>


